import {Joystick} from "react-joystick-component"


const GameController = ({ onJoystickMove }) => {
  return (
    <div className="joystick_wrap">
        <Joystick
          size={150}
          sticky={false} 
          baseColor="#050f1e" 
          stickColor="crimson"
          move={onJoystickMove}
          >
        </Joystick>
      </div>
  )
}

export default GameController