import { uuidv4 } from "@firebase/util"
import * as firebase from "firebase/database"
import db from "../firebase/firebase"

const create_room = async () => {
  let id = uuidv4().slice(0, 8)
  let rooms = firebase.ref(db, "rooms/")
  firebase.push(rooms, {
    id: id,
    status: "waitng",
  })
}

// create_room()

export default class Game {

  init(canvas) {
    this.cnv = canvas
    this.cnv.width = document.body.clientWidth
    this.cnv.height = document.body.clientWidth
    this.ctx = canvas.getContext("2d")

    // this.cnv_size
    this.MAP_SIZE = 20 // MAP_SIZExMAP_SIZE map
    this.CELL_SIZE = parseInt(this.cnv.width / this.MAP_SIZE)

    this.player = {
      pos: { x: 0, y: 0 },
      tail: [],
      color: "lime",
      tailColor: "green",
      direction: "RIGHT"
    }

    this.enemy = {
      pos: { x: this.cnv.width - this.CELL_SIZE, y: this.cnv.height - this.CELL_SIZE },
      color: "blue"
    }

    this.apple = {
      pos: { x: this.CELL_SIZE * 3, y: this.CELL_SIZE * 3 },
      color: "red"
    }

    this.apple.pos.x = 0

    this.ctx.font = "18px Arial"

    this.spawn_apple()

    window.onkeydown = (e) => {
      if (e.key === "w") this.player.direction = "FORWARD"
      if (e.key === "s") this.player.direction = "BACKWARD"
      if (e.key === "d") this.player.direction = "RIGHT"
      if (e.key === "a") this.player.direction = "LEFT"
    }

    this.game_loop = setInterval(
      this.update.bind(this),
      100
    )
    this.update()
  }

  draw_objects() {
    const apple = this.apple
    const pl = this.player
    const en = this.enemy

    this.ctx.fillStyle = apple.color
    this.ctx.fillRect(apple.pos.x, apple.pos.y, this.CELL_SIZE, this.CELL_SIZE)

    // tail
    this.ctx.fillStyle = pl.tailColor
    for (let i = 0; i < pl.tail.length; i++) {
      let part = pl.tail[i]
      this.ctx.fillRect(part.x, part.y, this.CELL_SIZE, this.CELL_SIZE)
    }
    // head
    this.ctx.fillStyle = pl.color
    this.ctx.fillRect(pl.pos.x, pl.pos.y, this.CELL_SIZE, this.CELL_SIZE)
    // title
    // let pl_title = `dmitry ${pl.tail.length + 1}`
    // this.ctx.fillStyle = "white"
    // this.ctx.fillText(
    //   pl_title, 
    //   pl.pos.x - pl_title.length*2, 
    //   pl.pos.y - 5
    // )

  }

  update_player_pos() {
    const pl = this.player
    const speed = this.CELL_SIZE
    const prevPos = {x: pl.pos.x, y: pl.pos.y}
    switch (pl.direction) {
      case "FORWARD":
        pl.pos.y -= speed
        break
      case "BACKWARD":
        pl.pos.y += speed
        break
      case "RIGHT":
        pl.pos.x += speed
        break
      case "LEFT":
        pl.pos.x -= speed
        break
      default:
        break
    }

    const intMapSize = this.CELL_SIZE*this.MAP_SIZE
    if (pl.pos.x < 0) {
      pl.pos.x = intMapSize-this.CELL_SIZE
    }
    if (pl.pos.x+this.CELL_SIZE > intMapSize) {
      pl.pos.x = 0
    }
    if (pl.pos.y < 0) {
      pl.pos.y = intMapSize-this.CELL_SIZE
    }
    if (pl.pos.y+this.CELL_SIZE > intMapSize) {
      pl.pos.y = 0
    }

    let prevTail = prevPos
    for (let i = pl.tail.length-1; i >= 0; i--) {
      let tmp = pl.tail[i]
      pl.tail[i] = {x: prevTail.x, y: prevTail.y}
      prevTail = tmp
    }
  }

  spawn_apple() {
    const x = parseInt(Math.random() * this.MAP_SIZE) * this.CELL_SIZE
    const y = parseInt(Math.random() * this.MAP_SIZE) * this.CELL_SIZE
    this.apple.pos = { x, y }
  }

  is_collide_with_cell(target, obs) {
    return parseInt(target.x) === parseInt(obs.x) &&
      parseInt(target.y) === parseInt(obs.y)
  }

  check_player_collision() {
    // with apple
    if (this.is_collide_with_cell(this.player.pos, this.apple.pos)) {
      this.spawn_apple()
      this.player.tail.push({
        x: this.player.pos.x,
        y: this.player.pos.y
      })
      return
    }
    // with tail
    // for (let i = 0; i < this.player.tail.length; i++) {
    //   let part = this.player.tail[i]
    //   if (this.is_collide_with_cell(this.player.pos, part)) {
    //     this.game_over()
    //   }
    // }
  }

  game_over() {
    while (this.player.tail.pop()) {}
  }

  update() {
    this.ctx.fillStyle = "black"
    this.ctx.fillRect(0, 0, this.cnv.width, this.cnv.height)

    this.draw_objects()
    this.check_player_collision()
    this.update_player_pos()
  }

  onJoystickMove(e) {
    this.player.direction = e.direction
  }



} 